import { JSX } from "react"
import { DocumentsCollectionAudioItem } from "@rp/react/tanstack-api/documents"
import { Button, Divider, Slider, Stack, Typography } from "@rp/react/web/mui/core"
import { Close, Pause, PlayArrow } from "@rp/react/web/mui/icons"
import { AudioPlayerInterface } from "./audioPlayer.types"
import { convertSecondsToTime, normalizeCurrentTimeChange } from "./audioPlayer.utils"

type AudioPlayerProps = Omit<AudioPlayerInterface, "audio" | "areControlsVisible"> & {
  audioItem: DocumentsCollectionAudioItem | null
  leftComponent: JSX.Element
}

const AudioPlayer = ({
  audioItem,
  closeControls,
  currentTime,
  duration,
  isPlaying,
  leftComponent,
  pause,
  play,
  updateCurrentTime,
}: AudioPlayerProps) => {
  const handleCurrentTimeChange = (_ev: Event, newValue: number | Array<number>) =>
    updateCurrentTime(normalizeCurrentTimeChange(newValue))

  if (!audioItem) return null

  return (
    <Stack direction="row" sx={{ width: "100%", alignItems: "center", margin: "12px" }}>
      {leftComponent && (
        <>
          {leftComponent}
          <Divider orientation="vertical" sx={{ height: "36px", ml: 1.5 }} />
        </>
      )}

      <Button
        onClick={isPlaying ? pause : play}
        size="small"
        sx={{ color: "BDprimary.200", mr: "8px", p: "8px 16px", minWidth: 0 }}
      >
        {isPlaying ? <Pause /> : <PlayArrow />}
      </Button>

      <Typography sx={{ mr: 3, fontSize: "14px" }}>{convertSecondsToTime(currentTime)}</Typography>

      <Slider
        aria-label="song position"
        sx={{
          flexGrow: 1,
          "& .MuiSlider-thumb": {
            color: "BDprimary.200",
          },
          "& .MuiSlider-track": {
            color: "BDprimary.200",
          },
          "& .MuiSlider-rail": {
            color: "grey.300",
          },
          "& .MuiSlider-active": {
            color: "BDprimary.200",
          },
        }}
        size="small"
        value={currentTime}
        onChange={handleCurrentTimeChange}
        min={0}
        max={duration}
      />

      <Typography sx={{ ml: 3, fontSize: "14px" }}>{convertSecondsToTime(duration)}</Typography>

      <Button onClick={closeControls}>
        <Close sx={{ color: "text.secondary" }} />
      </Button>
    </Stack>
  )
}
export { AudioPlayer }
